<template>
  <div>
    <br />
    <Row :gutter="24">
      <Col :sm="24" :lg="12">
        <Tree :data="treeData" :render="renderContent"></Tree>
      </Col>
      <Col :sm="24" :lg="12">
        <Card>
          <p slot="title">详细信息</p>
        </Card>
      </Col>
    </Row>
    
    <Modal v-model="addModal" title="机构名称" @on-ok="saveOrgTitle">
      <Input v-model="orgTitle" :placeholder="placeholder"></Input>
    </Modal>
     <Modal v-model="departmentModal" title="部门管理">
      <Form ref="departmentForm" :model="departmentForm">
        <FormItem label="部门编号">
          <Input v-model="departmentForm.no" clearable></Input>
        </FormItem>
        <FormItem label="部门名称">
          <Input v-model="departmentForm.no" clearable></Input>
        </FormItem>
        <FormItem label="部门类型">
          <Input v-model="departmentForm.no" clearable></Input>
        </FormItem>
        <FormItem label="负责人">
          <Input v-model="departmentForm.no" clearable></Input>
        </FormItem>
        <FormItem label="联系电话">
          <Input v-model="departmentForm.no" clearable></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="mgrModal = false">关闭</Button>
        <Button type="success">保存</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
export default {
  name: "OrgMgr",
  data() {
    return {
      addModal: false,
      departmentModal: false,
      isAdd: true,
      orgTitle: "",
      placeholder: "",
      currentData: "", // 当前点击操作节点的数据
      departmentForm: {
        
      },
      treeData: [
        {
          title: "曙光医院",
          expand: true,
          render: (h, { root, node, data }) => {
            return h(
              "span",
              {
                style: {
                  display: "inline-block",
                  width: "100%"
                }
              },
              [
                h("span", [
                  h("Icon", {
                    props: {
                      type: "ios-folder-outline"
                    },
                    style: {
                      marginRight: "8px"
                    }
                  }),
                  h("span", data.title)
                ]),
                h(
                  "span",
                  {
                    style: {
                      display: "inline-block",
                      float: "right",
                      marginRight: "32px"
                    }
                  },
                  [
                    h("Button", {
                      props: Object.assign({}, this.buttonProps, {
                        icon: "ios-add",
                        type: "primary"
                      }),
                      style: {
                        width: "80px"
                      },
                      on: {
                        click: () => {
                          this.orgTitle = "";
                          this.addModal = true;
                          this.isAdd = true;
                          this.currentData = data;
                          this.placeholder = "请填写部门名称";
                        }
                      }
                    })
                  ]
                )
              ]
            );
          },
          children: [
            {
              title: "门诊部",
              expand: true,
              children: [
                {
                  title: "儿科",
                  expand: true
                },
                {
                  title: "妇科",
                  expand: true
                }
              ]
            },
            {
              title: "住院部",
              expand: true,
              children: [
                {
                  title: "放射科",
                  expand: true
                },
                {
                  title: "骨科",
                  expand: true
                }
              ]
            }
          ]
        }
      ],
      buttonProps: {
        type: "default",
        size: "small"
      }
    };
  },
  methods: {
    // 树渲染
    renderContent(h, { root, node, data }) {
      return h(
        "span",
        {
          style: {
            display: "inline-block",
            width: "100%"
          }
        },
        [
          h("span", [
            h("Icon", {
              props: {
                type: "ios-paper-outline"
              },
              style: {
                marginRight: "8px"
              }
            }),
            h("span", data.title)
          ]),
          h(
            "span",
            {
              style: {
                display: "inline-block",
                float: "right",
                marginRight: "32px"
              }
            },
            [
              h("Button", {
                props: Object.assign({}, this.buttonProps, {
                  icon: "ios-add",
                  type: "primary"
                }),
                style: {
                  marginRight: "4px"
                },
                on: {
                  click: () => {
                    this.orgTitle = "";
                    this.addModal = true;
                    this.isAdd = true;
                    this.currentData = data;
                    this.placeholder = "请填写科室名称";
                  }
                }
              }),
              h("Button", {
                props: Object.assign({}, this.buttonProps, {
                  icon: "ios-create",
                  type: "success"
                }),
                style: {
                  marginRight: "4px"
                },
                on: {
                  click: () => {
                    this.orgTitle = data.title;
                    this.addModal = true;
                    this.isAdd = false;
                    this.currentData = data;
                  }
                }
              }),
              h("Button", {
                props: Object.assign({}, this.buttonProps, {
                  icon: "ios-remove"
                }),
                on: {
                  click: () => {
                    this.remove(root, node, data);
                  }
                }
              })
            ]
          )
        ]
      );
    },
    // 添加子节点
    append(data) {
      const children = data.children || [];
      children.push({
        title: this.orgTitle,
        expand: true
      });
      this.$set(data, "children", children);
    },
    // 修改名称
    updateOrgTitle(data) {
      this.$set(data, "title", this.orgTitle);
    },
    // 删除当前节点
    remove(root, node, data) {
      const parentKey = root.find(el => el === node).parent;
      const parent = root.find(el => el.nodeKey === parentKey).node;
      const index = parent.children.indexOf(data);
      parent.children.splice(index, 1);
    },
    // 保存机构名称
    saveOrgTitle() {
      if (this.isAdd) {
        this.append(this.currentData);
      } else {
        this.updateOrgTitle(this.currentData);
      }
    }
  }
};
</script>
